.home_page {
    width: 100%;
    margin: 1rem 0;

    h3 {
        a {
            text-transform: uppercase;
            text-decoration: none;
            color: $color-darkblue;
            letter-spacing: 3;
            margin-top: 1rem;
            cursor: pointer;
        }

        &:hover {
            a {
                color: $color-crimson;  
            } 
        }

        small {
            font-size: 15px;
        }
    }
}

.home_blogs {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
    grid-gap: 10px;
    margin-bottom: 15px;
}

