.blogs_category {
    width: 100%;
    margin: 1rem 0;
}

.blogs_category{
    &_show_blogs {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
        grid-gap: 10px;
        margin-bottom: 15px;
    
        &card,
        a {
            text-decoration: none;
            text-transform: capitalize;
        }
    }
}


